import {CREATE_TEAM} from "../Actions"

const initialState = []

let id=0;

export default (state =initialState, action) => {
    switch (action.type) {

    case CREATE_TEAM:
       state=[...state, {
        id:id=id+1,
        name:action.name,
        ttype:action.ttype,
        users:action.users
   }]
       return state
   
    default:
        return state
    }
}
